.callback-pop-up {
	display: none;
	position: fixed;
	z-index: 100;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8); }

.pop-up {
	@include css-lock(14, 16, 480, 886);
	@include vcenter;
	// width: 886px
	width: em(886, 16);
	// height: 398px
	min-height: em(398, 16);
	background-color: #ffffff;
	padding: em(40, 16);
	@include r(886) {
		width: 95%;
		padding: 15px; }

	&-close {
		position: fixed;
		right: em(37, 16);
		top: em(40, 16);
		display: block;
		width: 16px;
		height: 16px;
		@include s(close);
		@include r(360) {
			top: 10px;
			right: 10px; } }

	&__title {
		color: #3e3e3e;
		font-family: "Open Sans";
		font-size: 24px;
		@include css-lock(18, 24, 480, 886);
		font-weight: 600;
		line-height: em(30, 24);
		text-transform: uppercase;
		margin-top: em(32, 24);
		margin-bottom: em(16, 24); }
	&__img {
		transform: scale(0.74);
		float: left;
		margin: em(45, 16) em(50, 16) 0 em(18, 16);
		@include r(886) {
			margin: 0;
			margin-top: em(45, 16); }
		@include r(764) {
			display: none; } }
	&__form {
		display: inline-block;
		width: em(527, 16);
		@include r(764) {
			display: block;
			width: 100%; } }
	&__label {
		display: block;
		color: #3e3e3e;
		font-style: italic;
		line-height: 36px;
		font-weight: normal; }
	&__input {
		display: block;
		padding: 0 16px;
		width: 100%;
		height: em(40, 16);
		border: 1px solid #a9a9a9;
		background-color: #f5f5f5; }

	&__submit {
		width: em(256, 16);
		height: em(40, 16);
		background-color: #db5a31;
		color: #ffffff;
		font-weight: 600;
		line-height: em(40, 16);
		margin-top: em(32, 16);
		float: right; } }
