$main-color: #a9cf54;

@mixin title {
	color: #ffffff;
	font-family: "Open Sans";
	font-size: 48px;
	@include css-lock(28,48,480,1600);
	font-weight: normal;
	line-height: 1.5;
	text-transform: uppercase;
	text-align: center; }


body {
  background: #FFF;
  @include css-lock(14, 18, 480, 1600);
  line-height: 1.2;
  font-family: "Open Sans", sans-serif;
  font-weight: normal; }

.wrapper {
	max-width: 1440px;
	margin: 0 auto;
	position: relative;
	@include clr;
	@include r(1440) {
		padding: 0 5px; } }

.title-h2 {
	@include title; }

.is-blue {
	background-color: rgba(4, 191, 191, 0.8); }

.button {
	color: #ffffff;
	font-weight: 600;
	text-transform: uppercase;
	width: 16.28em;
	height: 4.1112em;
	border-radius: 7px;
	border: 6px solid rgba(255, 255, 255, 0.5);
	background-color: #db5a31;
	position: relative;
	transition: 0.3s;
	&:hover {
		border-color: rgba(219, 90, 49, 0.75);
		color: #db5a31;
		background-color:  #fff;
		padding-left: 0; } }

.button-2 {
	display: block;
	color: #ffffff;
	font-family: "Open Sans";
	font-weight: 600;
	text-transform: uppercase;
	width: 15.56em;
	height: 3.5em;
	border-radius: 7px;
	border: 1px solid rgba(76, 76, 76, 0.2);
	background-color: rgba(88, 143, 39, 0.2);
	// opacity: 0.2;
	margin: 1em auto; }

.talking-icon {
	display: inline-block;
	@include s(talking);
	position: absolute;
	top: 0.75em;
	left: 1.5em; }
