
.about {
	@include css-lock(14, 18, 480, 1600);
	background-color: $main-color;
	padding: 3.4em 0;
	@include r(1200) {
		padding: 1em 0; }
	&__text {
		margin: 3em 0;
		text-align: justify;
		@include clr;
		@include r(1200) {
			margin: 1em 0; }
		&_left {
			width: 47.5%;
			float: left;
			@include r(800) {
				float: none;
				width: 100%;
				margin: 10px 0; } }
		&_right {
			float: right;
			width: 47.5%;
			@include r(800) {
				float: none;
				width: 100%;
				margin: 10px 0; } } } }

